.page-header {
  position: relative;
  z-index: 20;
  padding: 20px 0;
  background-color: $main-color;
  box-shadow: 0px 25px 50px rgba(0, 0, 0, 0.25);

  @media (min-width: $screen-md) {
    padding: 19px 0 18px;
  }

  @media (min-width: $screen-lg) {
    padding: 14.5px 0 14.5px;
  }

  $block-name:                &; // #{$block-name}__element

  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: flex-start;

    .logo {
      margin-right: auto;
    }

    @media (min-width: $screen-md) {
      align-items: center;
    }
  }

  &__menu-block {
    display: inline-flex;

    @media (min-width: $screen-md) {
      order: 1;
      margin-left: 40px;
    }
  }

  &__icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: $white;

    &:last-child {
      margin-left: 30px;
    }

    span > svg {
      display: block;
      width: 32px;
      height: 32px;
      fill: currentColor !important;
    }

    &:hover,
    &:focus {
      color: $white;

      #{$block-name}__icon-text {
        text-decoration: underline;
      }
    }

    &[aria-expanded="true"] {

      #{$block-name}__icon-menu {
        display: none !important;
      }

      #{$block-name}__icon-close {
        display: block !important;
      }
    }
  }

  &__icon-menu {
    display: block;
  }

  &__icon-close {
    display: none !important;
  }

  &__icon-text {
    margin-top: 5px;
    font-size: 12px;
    font-weight: 600;
  }

  &__service-block {
    flex: 1 1 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 18px;

    @media (min-width: $screen-md) {
      flex: 0 1 330px;
      margin-top: 0;
    }

    @media (min-width: $screen-lg) {
      flex: 0 1 auto;
    }
  }

  &__callback-link {
    display: none;

    @media (min-width: $screen-lg) {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      width: auto;
      margin-left: 22px;
      margin-right: 40px;
      color: $white;
      font-size: 20px;
      font-weight: 600;
      text-decoration: none;

      &:after {
        position: absolute;
        left: 0;
        bottom: -2px;
        display: block;
        content: "";
        height: 2px;
        width: 0%;
        background-color: $white;
        transition: width .2s ease-out;
      }

      &:hover,
      &:focus {
        color: $white;
        text-decoration: none;

        &:after {
          width: 100%;
        }
      }
    }
  }
}
