.cover {

  $block-name:                &; // #{$block-name}__element

  &__map {
    margin-bottom: 40px;
    box-shadow: 0px 10px 16px rgba(22, 65, 255, 0.16), 0px 4px 6px rgba(0, 0, 0, 0.06);

    @media (min-width: $screen-xl) {
      margin-bottom: 60px;
    }
  }

  &__map-container {
    height: 350px;

    @media (min-width: $screen-md) {
      height: 400px;
    }

    @media (min-width: $screen-lg) {
      height: 500px;
    }

    @media (min-width: $screen-xl) {
      height: 650px;
    }

    iframe {
      display: block;
      width: 100%;
      height: 100%;
      border: none;
    }
  }

  &__address-list {
    padding-bottom: 20px;
    column-count: 1;

    @media (min-width: $screen-md) {
      column-count: 2;
      column-gap: 30px;
    }

    @media (min-width: $screen-lg) {
      padding-bottom: 40px;
      column-count: 3;
      column-gap: 60px;
    }
  }

  &__address-item {
    display: inline-block;
    vertical-align: top;
    width: 100%;
    margin-bottom: 20px;
  }

  &__address-street {
    display: block;
    margin-bottom: 0.5em;
    font-size: $font-size--h4;
    font-weight: 700;
    color: $main-color;

    @media (min-width: $screen-lg) {
      font-size: $font-size--h4 * 1.111;
    }

    @media (min-width: $screen-xl) {
      font-size: $font-size--h4 * 1.333;
    }
  }

  &__address-houses {
    display: block;
  }
}
