@font-face {
  font-family: 'Inter';
  src: url('../fonts/Inter-Regular.woff2') format('woff2'),
       url('../fonts/Inter-Regular.woff') format('woff');
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url('../fonts/Inter-Medium.woff2') format('woff2'),
       url('../fonts/Inter-Medium.woff') format('woff');
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url('../fonts/Inter-SemiBold.woff2') format('woff2'),
       url('../fonts/Inter-SemiBold.woff') format('woff');
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Inter';
  src: url('../fonts/Inter-Bold.woff2') format('woff2'),
       url('../fonts/Inter-Bold.woff') format('woff');
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}


body {
  //  !
  font-family: $font-family;
  font-size: $font-size;
  font-weight: 400;
  line-height: $line-height;
  word-break: break-word;
  text-align: left;
  color: $text-color;
  background-color: $body-bg;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

    @media (min-width: $screen-lg) {
      font-size: $font-size * 1.125;
    }

    @media (min-width: $screen-xl) {
      font-size: $font-size * 1.25;
    }

  // text-rendering: optimizeLegibility;
  // Partial support in Android browser versions is due to a serious bug where text-rendering: optimizeLegibility causes custom web fonts to not render!!!
}

.h1,
.h2,
.h3,
.h4,
.h5,
.h6,
h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 1.5em;
  margin-bottom: 20px;
  font-family: $font-family;
  font-weight: 700;
  line-height: 1.3;
  color: $title-color;
}

.h1,
h1 {
  margin-bottom: 20px;
  font-size: $font-size--h1;

  @media (min-width: $screen-md) {
    margin-bottom: 30px;
  }

  @media (min-width: $screen-lg) {
    margin-bottom: 40px;
    font-size: $font-size--h1 * 1.235;
  }

  @media (min-width: $screen-xl) {
    margin-bottom: 60px;
    font-size: $font-size--h1 * 1.529;
  }
}

.h2,
h2 {
  font-size: $font-size--h2;

  @media (min-width: $screen-md) {
    margin-bottom: 30px;
  }

  @media (min-width: $screen-lg) {
    margin-bottom: 40px;
    font-size: $font-size--h2 * 1.143;
  }

  @media (min-width: $screen-xl) {
    font-size: $font-size--h2 * 1.429;
  }
}

.h3,
h3 {
  font-size: $font-size--h3;

  @media (min-width: $screen-lg) {
    font-size: $font-size--h3 * 1.091;
  }

  @media (min-width: $screen-xl) {
    font-size: $font-size--h3 * 1.455;
  }
}

.h4,
h4 {
  font-size: $font-size--h4;

  @media (min-width: $screen-lg) {
    font-size: $font-size--h4 * 1.111;
  }

  @media (min-width: $screen-xl) {
    font-size: $font-size--h4 * 1.333;
  }
}

.h5,
.h6,
h5,
h6 {
  font-size: $font-size--h5;

  @media (min-width: $screen-md) {
    font-size: $font-size--h5 * 1.125;
  }

  /* @media (min-width: $screen-xl) {
    font-size: $font-size--h5 * 1.25;
  } */
}

div,
section,
article {

  *:first-child {
    margin-top: 0;
  }
}

.p,
p,
ul,
ol,
dl,
blockquote,
pre,
address,
figure {
  //margin-top: 0.5em;
  // margin-bottom: 0.5em;
  color: currentColor;

  & + & {
    margin-top: 0.5em;
  }
}

address {
  font-style: normal;
}

ol {
  padding-left: 1.3em;
}

ul {
  padding-left: 20px;
  list-style: none;

  @media (min-width: $screen-lg) {
    padding-left: 30px;
  }

  li {
    position: relative;

      &::before {
      display: block;
      content: "";
      position: absolute;
      top: 0.5em;
      left: -20px;
      width: 8px;
      height: 8px;
      border-radius: 50%;
      background-color: $main-color;

      @media (min-width: $screen-lg) {
        top: 0.55em;
        left: -30px;
      }
    }
  }
}

ol ol,
ul ul,
ol ul,
ul ol {
  margin-top: 0;
  margin-bottom: 0;
}

li {
  line-height: inherit;
}

hr {
  box-sizing: content-box; //  Firefox
  height: 0;               //  Firefox
  overflow: visible;       //  Edge  IE
  margin-top: 1em;
  margin-bottom: 1em;
  border: 0;
  border-top: 1px solid $border-color;
}

.small,
small {
  font-size: $font-size * 0.75;
}

sub,
sup {
  font-size: 65%;
}

b,
strong {
  font-weight: bolder;
}

abbr[title] {
  text-decoration: underline dotted;
}

blockquote {
  padding: 0;
  margin-left: 0;
  margin-right: 0;
}

dt {
  font-weight: 700;
}

dd {
  margin-left: 0;

  @media (min-width: $screen-sm) {
    margin-left: 1.5rem;
  }

  & + dt {
    margin-top: 0.5em;
  }
}

// samp,
code,
kbd,
pre {
  // font-family: $font-family--monospace;
}

code {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: rgba(#000, 0.08);
  border-radius: $border-radius;
}

kbd {
  padding: 0.06em 0.3em;
  color: $text-color;
  background-color: $gray-lightest;
  border-radius: $border-radius;

  kbd {
    padding: 0;
  }
}

pre {
  display: block;
  width: 100%;
  margin-bottom: 0.5em;
  overflow-x: auto;
  tab-size: 2;
  background-color: rgba(#000, 0.08);

  code {
    background: transparent;
  }
}

